import { changesExpression } from "./data.js";

const debugExpression = (screen) => {
  let newExpression = screen.lScreen.textContent;
  for (let change in changesExpression) {
    newExpression.includes(change) &&
      (newExpression = newExpression.replace(
        change,
        changesExpression[change]
      ));
  }

  newExpression.includes("Ans") &&
    (newExpression = newExpression.replace("Ans", screen.ans));

  return newExpression;
};

export default debugExpression;
